
.container {
    margin: 0 auto;
    height: 100vh;
    width: 500px;
}

h1 {
    text-align: center;
}

.header {
    display: flex;
    max-width: 100%;
}

.input {
    width: 100%;
}

.task-wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.todo {
    background-color: #fff;
}

.done {
    background-color: rgb(229, 228, 228);
}

